.layout__wrap{
    position: relative;
}
.layout__img{
    position: absolute;
    top: 0;
    right: 70px;
    width: 739px;
}
.layout__text-wrap{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 480px;
    padding-top: 50px;
    margin-left: 100px;
    font-weight: 700;
    text-align: start;

}

.layout__game{
    color: #E87D0E;
    font-size: 20px;
    
    line-height: 155.2%;
}
h1{
    font-size: 50px;
    font-weight: 700;
    
    line-height: 155.2%;
}
.layout__paragraph{
    font-size: 1rem;
    font-weight: 500;
    line-height: 200%;
    margin-bottom: 50px;
}
.details{
    max-width: 195px;
    width: 100%;
    padding: 15px 30px;
    height: 53px;
    border-radius: 40px;
    background: linear-gradient(113deg, #FA8305 0%, #FB9E3C 100%);

}