.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 88px;
}
.logo{
    width: fit-content;
    font-size: 29px;
    font-weight: 800;
    line-height: 44px;
}
.nav{
    display: flex;
    gap: 35px;
}
.header__list{
    display: flex;
    align-items: center;
    max-width: 505px;
    width: 100%;
   position: relative;
   gap: 88px;
   padding: 0;
}
.header__list-link{
    display: flex;
    font-size: 1rem;
    font-weight: 400;
    line-height: 24px;
    width: 100%;
    padding: 0;
    min-width: fit-content;
    position: relative;
}


.hoverAffect{
    width: 39px;
    position: absolute;
    bottom: -9px;
    left: -40%; 
}

.contact{
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 132px;
    width: 100%;
    font-size: 13px;
    line-height: 19px;
    border-radius: 6px;
    padding: 11px 28px;
}